.header {
  margin: auto;
  margin-top: 0.5rem;
  padding-left: 10px;
  justify-content: space-between;
  width: 100%;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: 0.5rem 1rem;
  border-radius: 3px;
  cursor: pointer;
}

.header ul li:hover {
  background-color: grey;
  color: white;
}

.header a:hover {
  text-decoration: none;
}

@media screen and (max-width: 500px) {
  .header ul {
    flex-direction: column;
  }
}
